
:root {
  --bg: #333;
  --fg: #ccc;
  --outline: hsla(0, 0%, 33%, 0.5);
  --outline-fg: hsla(0, 0%, 60%, 0.5);
  --okay: #c93;
  --good: #496;
  --tile-entry: #111;
  --tile-nope: var(--outline);
  --tile-lost: var(--okay);
  --tile-found: var(--good);
}
html, body {
