button {
  --bg-top: #555;
  --bg-bottom: hsla(0, 0%, 33.3%, 0.25);
  background: -webkit-gradient(linear, left top, left bottom, color-stop(10%, var(--bg-top)), color-stop(140%, var(--bg-bottom)));
  background: linear-gradient(var(--bg-top) 10%, var(--bg-bottom) 140%);
  border: 2px solid var(--outline);
  border-top-width: 1px;
  border-radius: 0.5em;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: var(--fg);
  font: inherit;
  font-weight: bold;
  padding: 0.15em 0.4em;
  text-align: center;
  text-shadow: 1px 1px 0 hsla(0, 0%, 0%, 0.5);
  text-transform: uppercase;
  -ms-touch-action: manipulation;
      touch-action: manipulation; /* try to prevent page zoom on double-button-tap */
}
button:hover {
  --bg-bottom: hsla(0, 0%, 33.3%, 0.667);
  --outline: #888;
}
button:active {
  --bg-bottom: hsla(0, 0%, 66.7%, 0.333);
  --outline: #666;
}
button:disabled,
button:disabled:hover,
button:disabled:active {
  --bg-top: var(--bg-bottom);
  --bg-bottom: hsla(0, 0%, 33.3%, 0.25);
  --outline: hsla(0, 0%, 33%, 0.5);
  opacity: 0.5;
  cursor: not-allowed;
}
a {
  color: inherit;
}


